import axios from "axios";
import {HTTP,HTTPS} from "@constants/api";

/**
* –ò–∑–º–µ–Ω—è–µ—Ç URL —Å HTTP –Ω–∞ HTTPS
* @param {String} url - url –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è
* @returns {String} -url —Å HTTPS
*
*/
export const changeHTTP = url => {
    const result = url ? url.replace(HTTP,HTTPS) : url;
    return result
}

/**
 * –û—Ç–ø—Ä–∞–≤–ª—è–µ—Ç –∑–∞–ø—Ä–æ—Å axios
 * @param {String} url - url –î–ª—è –∑–∞–ø—Ä–æ—Å–∞
 * @returns {Promise} = Promise —Å —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–º –∑–∞–ø—Ä–æ—Å–∞
 */

export const getApiResource = async (url) => {

    try {
        const res = await axios.get(url);
        if(res.resultCode === 1){
            console.error("Could not fetch",res.status)
            return false
        }
        return await res.data;
    } catch(error) {
        console.error("Could not fetch", error.message)
        return false
    }

}

